.application__input {
  outline: none;
}

.application__icon-email {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 14px;
  right: 15px;

  fill: none;
  stroke: $specialdarkgrey;
}

.application__icon-phone {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 14px;
  right: 13px;

  fill: $specialdarkgrey;
}

.application__input:focus + .application__icon-email {
  stroke: $basicgreen;
}

.application__input:focus + .application__icon-phone {
  fill: $basicgreen;
}

.application__input:invalid:not(:placeholder-shown) + .application__icon-email {
  stroke: #ff8282;
}

.application__input:invalid:not(:placeholder-shown) + .application__icon-phone {
  fill: #ff8282;
}

.application__end-wrapper {
  margin-top: 4px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;

    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    column-gap: 80px;
  }
}

.application__button {
  line-height: 20px;

  @media (min-width: $tablet-width) {
    width: 282px;
    height: 52px;

    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.application__note {
  margin: 22px 0 2px 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
  text-align: center;

  color: black;

  @media (min-width: $tablet-width) {
    margin: 17px 0 0 0;

    font-size: 16px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    margin: 18px 0 0 0;
  }
}
